import {
  computed, reactive, Ref, UnwrapRef,
} from '@vue/composition-api';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';

interface TextElement {
  readonly text: Ref<string | undefined>;
  readonly hasElement: Ref<boolean>;
}

export function useTextElement(
  content: Ref<string | undefined>,
  design: Ref<TextSettings | undefined>,
): UnwrapRef<TextElement> {
  const text = computed(() => content.value);

  const hasElement = computed(() => {
    const isVisible = design.value?.visible ?? false;
    const hasText = isNotNullOrEmpty(text.value);
    return isVisible && hasText;
  });

  return reactive({
    text,
    hasElement,
  });
}

export default {};
