import {
  computed,
  onMounted, onUnmounted, Ref, ref,
} from '@vue/composition-api';
import { ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';
import { GeneralDesignSettings } from '@/app/shared/client/type/settings/general-settings';
import { GlobalData } from '@/app/shared/client/type/tile/global';
import { useWindowScrollSettings } from '@/app/shared/client/logic/scroll-settings';
import { useTileOffsetSettings } from '@/app/shared/client/logic/offset-settings';
import { checkIfTileShown } from '@/app/website/client/logic/appearance-behaviour';
import { checkIfTileTransparent } from '@/app/website/client/logic/tile-properties';

interface TileBase {
  readonly tileElementRef: Ref<HTMLElement | undefined>;
  readonly isTileShown: Ref<boolean>;
  readonly hasTransparentBackground: Ref<boolean>;
}

export function useTileBase(
  emit: (event: string, ...args: unknown[]) => void,
  viewportSettings: ViewportSettings,
  siteSettings: Ref<GlobalData | undefined>,
  generalDesign: Ref<GeneralDesignSettings | undefined>,
): TileBase {
  const tileElementRef = ref<HTMLElement>();

  const windowScrollSettings = useWindowScrollSettings();
  const tileOffsetSettings = useTileOffsetSettings(tileElementRef);

  const isTileShowRef = ref(false);
  const isTileShown = computed(() => checkIfTileShown(
    isTileShowRef,
    viewportSettings,
    windowScrollSettings.windowScrollTop.value,
    tileOffsetSettings.tileOffsetTop.value,
    tileOffsetSettings.tileOffsetHeight.value,
  ));

  const hasTransparentBackground = computed(() => checkIfTileTransparent(
    generalDesign.value,
    siteSettings.value?.design,
  ));

  onMounted(() => {
    emit('tile-loaded');
    windowScrollSettings.subscribeOnWindowScrollChange();
    tileOffsetSettings.subscribeOnTileOffsetChange();
  });

  onUnmounted(() => {
    emit('tile-unloaded');
  });

  return {
    tileElementRef,
    isTileShown,
    hasTransparentBackground,
  };
}

export default {};
