import {
  computed, reactive, Ref, UnwrapRef,
} from '@vue/composition-api';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { ContactInfoData } from '@/app/shared/client/type/contact-info';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';

interface ContactInfoElement {
  readonly subheading: Ref<string | undefined>;
  readonly phone: Ref<string | undefined>;
  readonly email: Ref<string | undefined>;
  readonly hasSubheading: Ref<boolean>;
  readonly hasPhone: Ref<boolean>;
  readonly hasEmail: Ref<boolean>;
}

export function useContactInfoElement(
  content: Ref<ContactInfoData | undefined>,
  subheadingDesign: Ref<TextSettings | undefined>,
  phoneDesign: Ref<TextSettings | undefined>,
  emailDesign: Ref<TextSettings | undefined>,
): UnwrapRef<ContactInfoElement> {
  const subheading = computed(() => content.value?.subheading);
  const phone = computed(() => content.value?.phone);
  const email = computed(() => content.value?.email);

  const hasSubheading = computed(() => {
    const isSubheadingVisible = subheadingDesign.value?.visible ?? false;
    const hasSubheadingContent = isNotNullOrEmpty(subheading.value);
    return isSubheadingVisible && hasSubheadingContent;
  });

  const hasPhone = computed(() => {
    const isPhoneVisible = phoneDesign.value?.visible ?? false;
    const hasPhoneContent = isNotNullOrEmpty(phone.value);
    return isPhoneVisible && hasPhoneContent;
  });

  const hasEmail = computed(() => {
    const isEmailVisible = emailDesign.value?.visible ?? false;
    const hasEmailContent = isNotNullOrEmpty(email.value);
    return isEmailVisible && hasEmailContent;
  });

  return reactive({
    subheading,
    phone,
    email,
    hasSubheading,
    hasPhone,
    hasEmail,
  });
}

export default {};
