import {
  computed, reactive, Ref, UnwrapRef,
} from '@vue/composition-api';
import { ImageData } from '@/app/shared/client/type/image';
import { ImageSettings } from '@/app/shared/client/type/settings/image-settings';

interface ImageElement {
  readonly image: Ref<ImageData | undefined>;
  readonly hasElement: Ref<boolean>;
}

export function useImageElement(
  content: Ref<ImageData | undefined>,
  design: Ref<ImageSettings | undefined>,
): UnwrapRef<ImageElement> {
  const image = computed(() => content.value);

  const hasElement = computed(() => {
    const isVisible = design.value?.visible ?? false;
    const hasImage = image.value !== undefined;
    return isVisible && hasImage;
  });

  return reactive({
    image,
    hasElement,
  });
}

export default {};
