import {
  computed, reactive, Ref, UnwrapRef,
} from '@vue/composition-api';
import { SocialIconListData } from '@/app/shared/client/type/social-icon-list';
import { socialIconComponentName } from '@/app/shared/client/helper/social-icon-list';
import {
  SocialIconListAppearance,
  SocialIconListSettings,
} from '@/app/shared/client/type/settings/social-icon-list-settings';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';

interface SocialIcon {
  readonly icon: string;
  readonly link: string | undefined;
}

interface SocialIconListElement {
  iconList: Ref<Readonly<SocialIcon[]>>;
  readonly hasElement: Ref<boolean>;
  readonly isPlain: Ref<boolean>;
  readonly isSolidCircle: Ref<boolean>;
  readonly isOutlineCircle: Ref<boolean>;
}

export function useSocialIconListElement(
  content: Ref<SocialIconListData | undefined>,
  design: Ref<SocialIconListSettings | undefined>,
): UnwrapRef<SocialIconListElement> {
  const iconList = computed(() => {
    const list = content.value?.list ?? [];
    return list.map((icon) => ({
      type: icon.type,
      icon: socialIconComponentName(icon.type),
      link: isNotNullOrEmpty(icon.link) ? icon.link : undefined,
    }));
  });

  const hasElement = computed(() => {
    const isVisible = design.value?.visible ?? false;
    const hasContent = iconList.value.length > 0;
    return isVisible && hasContent;
  });

  const appearance = computed(() => design.value?.appearance);

  const isPlain = computed(
    () => appearance.value === SocialIconListAppearance.PLAIN,
  );
  const isSolidCircle = computed(
    () => appearance.value === SocialIconListAppearance.SOLID_CIRCLE,
  );
  const isOutlineCircle = computed(
    () => appearance.value === SocialIconListAppearance.OUTLINE_CIRCLE,
  );

  return reactive({
    iconList,
    hasElement,
    isPlain,
    isSolidCircle,
    isOutlineCircle,
  });
}

export default {};
