import { SocialIconType } from '@/app/shared/client/type/social-icon-list';
import { SocialIconListAppearance } from '@/app/shared/client/type/settings/social-icon-list-settings';

export function socialIconComponentName(socialIconType: SocialIconType | undefined): string {
  switch (socialIconType) {
    case SocialIconType.FACEBOOK:
      return 'FacebookIcon';
    case SocialIconType.INSTAGRAM:
      return 'InstagramIcon';
    case SocialIconType.TWITTER:
      return 'TwitterIcon';
    case SocialIconType.WHATSAPP:
      return 'WhatsappIcon';
    case SocialIconType.YOUTUBE:
      return 'YoutubeIcon';
    case SocialIconType.PINTEREST:
      return 'PinterestIcon';
    case SocialIconType.VKONTAKTE:
      return 'VkontakteIcon';
    case SocialIconType.MESSENGER:
      return 'MessengerIcon';
    case SocialIconType.TIKTOK:
      return 'TiktokIcon';
    case SocialIconType.YELP:
      return 'YelpIcon';
    default:
      throw new Error('Unknown social icon type');
  }
}

