export function socialIconListAppearanceLabel(appearance: SocialIconListAppearance): string {
  switch (appearance) {
    case SocialIconListAppearance.PLAIN:
      return 'CommonLabels.SocialIconList.Appearance.Plain';
    case SocialIconListAppearance.SOLID_CIRCLE:
      return 'CommonLabels.SocialIconList.Appearance.SolidCircle';
    case SocialIconListAppearance.OUTLINE_CIRCLE:
      return 'CommonLabels.SocialIconList.Appearance.OutlineCircle';
    default:
      throw new Error('Unknown social icon list appearance');
  }
}

