import {
  computed, reactive, Ref, UnwrapRef,
} from '@vue/composition-api';
import { isNotNullOrEmpty, isNullOrEmpty } from '@/app/shared/client/helper/base';
import { LocationData } from '@/app/shared/client/type/location';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { ImageSettings } from '@/app/shared/client/type/settings/image-settings';

const GOOGLE_MAP_EMBED_API_URL = 'https://www.google.com/maps/embed/v1/place';
const GOOGLE_MAP_EMBED_API_KEY = 'AIzaSyCNCmAGyN4bJYu5qeLgbASzZafm-M5TA_o';
const GOOGLE_MAP_EMBED_API_LANGUAGE = 'en';
const GOOGLE_MAP_EMBED_API_ZOOM = '16';
const GOOGLE_MAP_EMBED_API_MAP_TYPE = 'roadmap';
const GOOGLE_MAP_DIR_API_URL = 'https://www.google.com/maps/dir/?api=1';

interface LocationElement {
  readonly subheading: Ref<string | undefined>;
  readonly address: Ref<string | undefined>;
  readonly mapUrl: Ref<string>;
  readonly getDirectionsUrl: Ref<string>;
  readonly hasSubheading: Ref<boolean>;
  readonly hasAddress: Ref<boolean>;
  readonly hasMap: Ref<boolean>;
}

export function useLocationElement(
  content: Ref<LocationData | undefined>,
  subheadingDesign: Ref<TextSettings | undefined>,
  addressDesign: Ref<TextSettings | undefined>,
  mapDesign: Ref<ImageSettings | undefined>,
): UnwrapRef<LocationElement> {
  const subheading = computed(() => content.value?.subheading);
  const address = computed(() => content.value?.address);
  const postalAddress = computed(() => content.value?.postalAddress);

  const mapUrl = computed(() => {
    const url = new URL(GOOGLE_MAP_EMBED_API_URL);
    url.searchParams.set('key', GOOGLE_MAP_EMBED_API_KEY);
    url.searchParams.set('language', GOOGLE_MAP_EMBED_API_LANGUAGE);
    url.searchParams.set('zoom', GOOGLE_MAP_EMBED_API_ZOOM);
    url.searchParams.set('maptype', GOOGLE_MAP_EMBED_API_MAP_TYPE);

    if (!isNullOrEmpty(address.value)) {
      url.searchParams.set('q', address.value);
    }

    if (!isNullOrEmpty(postalAddress.value)) {
      url.searchParams.set('q', postalAddress.value);
    }

    return url.toString();
  });

  const getDirectionsUrl = computed(() => {
    const url = new URL(GOOGLE_MAP_DIR_API_URL);

    if (!isNullOrEmpty(address.value)) {
      url.searchParams.set('destination', address.value);
    }

    if (!isNullOrEmpty(postalAddress.value)) {
      url.searchParams.set('destination', postalAddress.value);
    }

    return url.toString();
  });

  const hasSubheading = computed(() => {
    const isSubheadingVisible = subheadingDesign.value?.visible ?? false;
    const hasSubheadingContent = isNotNullOrEmpty(subheading.value);
    return isSubheadingVisible && hasSubheadingContent;
  });

  const hasAddress = computed(() => {
    const isAddressVisible = addressDesign.value?.visible ?? false;
    const hasAddressContent = isNotNullOrEmpty(address.value);
    return isAddressVisible && hasAddressContent;
  });

  const hasMap = computed(() => {
    const isMapVisible = mapDesign.value?.visible ?? false;
    const hasAddressContent = isNotNullOrEmpty(address.value);
    const hasPostalAddressContent = isNotNullOrEmpty(postalAddress.value);
    return isMapVisible && (hasAddressContent || hasPostalAddressContent);
  });

  return reactive({
    subheading,
    address,
    mapUrl,
    getDirectionsUrl,
    hasSubheading,
    hasAddress,
    hasMap,
  });
}

export default {};
