import {
  computed, reactive, Ref, UnwrapRef,
} from '@vue/composition-api';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { HoursData } from '@/app/shared/client/type/hours';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';

interface HoursElement {
  readonly subheading: Ref<string | undefined>;
  readonly note: Ref<string | undefined>;
  readonly hasSubheading: Ref<boolean>;
  readonly hasNote: Ref<boolean>;
}

export function useHoursElement(
  content: Ref<HoursData | undefined>,
  subheadingDesign: Ref<TextSettings | undefined>,
  noteDesign: Ref<TextSettings | undefined>,
): UnwrapRef<HoursElement> {
  const subheading = computed(() => content.value?.subheading);
  const note = computed(() => content.value?.note);

  const hasSubheading = computed(() => {
    const isSubheadingVisible = subheadingDesign.value?.visible ?? false;
    const hasSubheadingContent = isNotNullOrEmpty(subheading.value);
    return isSubheadingVisible && hasSubheadingContent;
  });

  const hasNote = computed(() => {
    const isNoteVisible = noteDesign.value?.visible ?? false;
    const hasNoteContent = isNotNullOrEmpty(note.value);
    return isNoteVisible && hasNoteContent;
  });

  return reactive({
    subheading,
    note,
    hasSubheading,
    hasNote,
  });
}

export default {};
