































































































































































































import {
  computed, defineComponent, PropType, ref,
} from '@vue/composition-api';
import {
  StyleKeyValue,
  tileStyleSelector,
  generalStyles,
  textStyles,
  imageOverlayStyles,
  actionLinkStyles,
  imageStyles,
  colorStyles,
} from '@/app/website/client/logic/tile-style';
import {
  getImageSet,
  getImageStyleSelector,
} from '@/app/website/client/logic/image-style';
import {
  LocationTileContent,
  LocationTileDesign,
  LocationTileLayout,
} from '@/app/shared/client/type/tile/location-tile';
import {
  ImageSettings,
} from '@/app/shared/client/type/settings/image-settings';
import {
  ActionLinkSettings,
} from '@/app/shared/client/type/settings/action-link-settings';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import {
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { Breakpoints, ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';

import MapRight from '@/app/website/client/defaults/location/map-right.json';
import MapLeft from '@/app/website/client/defaults/location/map-left.json';
import ImageRight from '@/app/website/client/defaults/location/image-right.json';
import ImageLeft from '@/app/website/client/defaults/location/image-left.json';
import BackgroundRight from '@/app/website/client/defaults/location/background-right.json';
import BackgroundLeft from '@/app/website/client/defaults/location/background-left.json';
import Full from '@/app/website/client/defaults/location/full.json';
import Short from '@/app/website/client/defaults/location/short.json';
import {
  LocationTileLayoutDefaults,
} from '@/app/shared/client/type/defaults/location-tile-defaults';
import { State } from '@/app/website/client/store/state';
import { ImageData } from '@/app/shared/client/type/image';
import ActionLink from '@/app/website/client/view/component/ActionLink.vue';
import ArrowDownIconSmall from '@/app/website/client/view/icon/ArrowDownIconSmall.vue';
import DirectionsIcon from '@/app/website/client/view/icon/DirectionsIcon.vue';
import FacebookIcon from '@/app/website/client/view/icon/FacebookIcon.vue';
import InstagramIcon from '@/app/website/client/view/icon/InstagramIcon.vue';
import MessengerIcon from '@/app/website/client/view/icon/MessengerIcon.vue';
import PinterestIcon from '@/app/website/client/view/icon/PinterestIcon.vue';
import TiktokIcon from '@/app/website/client/view/icon/TiktokIcon.vue';
import TwitterIcon from '@/app/website/client/view/icon/TwitterIcon.vue';
import VkontakteIcon from '@/app/website/client/view/icon/VkontakteIcon.vue';
import WhatsappIcon from '@/app/website/client/view/icon/WhatsappIcon.vue';
import YelpIcon from '@/app/website/client/view/icon/YelpIcon.vue';
import YoutubeIcon from '@/app/website/client/view/icon/YoutubeIcon.vue';
import { useTileBase } from '@/app/website/client/logic/tile-base';
import { useTextElement } from '@/app/website/client/logic/element/text-element';
import { useImageElement } from '@/app/website/client/logic/element/image-element';
import { useLocationElement } from '@/app/website/client/logic/element/location-element';
import { useHoursElement } from '@/app/website/client/logic/element/hours-element';
import { useContactInfoElement } from '@/app/website/client/logic/element/contact-info-element';
import {
  useSocialIconListElement,
} from '@/app/website/client/logic/element/social-icon-list-element';
import {
  getImageInUploadingState,
} from '@/app/website/client/logic/image-uploading';
import FontLoader from '@/app/website/client/view/component/FontLoader.vue';
import { hasActionLinkElement } from '@/app/website/client/logic/element/action-link-element';
import useI18N from '@/app/shared/client/logic/i18n';
import TileTitle from '@/app/website/client/view/component/TileTitle.vue';

function getDefaults(
  layout: LocationTileLayout | undefined,
): LocationTileLayoutDefaults | undefined {
  switch (layout) {
    case 'MAP_RIGHT':
      return MapRight as LocationTileLayoutDefaults;
    case 'MAP_LEFT':
      return MapLeft as LocationTileLayoutDefaults;
    case 'IMAGE_RIGHT':
      return ImageRight as LocationTileLayoutDefaults;
    case 'IMAGE_LEFT':
      return ImageLeft as LocationTileLayoutDefaults;
    case 'BACKGROUND_RIGHT':
      return BackgroundRight as LocationTileLayoutDefaults;
    case 'BACKGROUND_LEFT':
      return BackgroundLeft as LocationTileLayoutDefaults;
    case 'FULL':
      return Full as LocationTileLayoutDefaults;
    case 'SHORT':
      return Short as LocationTileLayoutDefaults;
    default:
      return undefined;
  }
}

function computedLayoutClass(layout: LocationTileLayout | undefined): string {
  switch (layout) {
    case 'MAP_RIGHT':
      return 'map-right';
    case 'MAP_LEFT':
      return 'map-left';
    case 'IMAGE_RIGHT':
      return 'image-right';
    case 'IMAGE_LEFT':
      return 'image-left';
    case 'BACKGROUND_RIGHT':
      return 'background-right';
    case 'BACKGROUND_LEFT':
      return 'background-left';
    case 'FULL':
      return 'full';
    case 'SHORT':
      return 'short';
    default:
      return 'image-left';
  }
}

function getDesignSettings(
  userDesign: LocationTileDesign,
  defaults?: LocationTileLayoutDefaults,
): LocationTileDesign {
  const general: GeneralDesignSettings = {
    ...defaults?.general,
    ...userDesign.general,
    background: {
      ...defaults?.general?.background,
      ...userDesign.general?.background,
      solid: {
        ...defaults?.general?.background?.solid,
        ...userDesign.general?.background?.solid,
      },
      gradient: {
        ...defaults?.general?.background?.gradient,
        ...userDesign.general?.background?.gradient,
      },
    },
  };

  const title: TextSettings = {
    ...(defaults?.title as TextSettings),
    ...userDesign.title,
  };

  const description: TextSettings = {
    ...(defaults?.description as TextSettings),
    ...userDesign.description,
  };

  const locationSubheading: TextSettings = {
    ...(defaults?.locationSubheading as TextSettings),
    ...userDesign.locationSubheading,
  };

  const locationAddress: TextSettings = {
    ...(defaults?.locationAddress as TextSettings),
    ...userDesign.locationAddress,
  };

  const locationMap: ImageSettings = {
    ...(defaults?.locationMap as ImageSettings),
    ...userDesign.locationMap,
    overlay: {
      ...defaults?.locationMap?.overlay,
      ...userDesign.locationMap?.overlay,
      solid: {
        ...defaults?.locationMap?.overlay?.solid,
        ...userDesign.locationMap?.overlay?.solid,
      },
      gradient: {
        ...defaults?.locationMap?.overlay?.gradient,
        ...userDesign.locationMap?.overlay?.gradient,
      },
    },
  };

  const hoursSubheading: TextSettings = {
    ...(defaults?.hoursSubheading as TextSettings),
    ...userDesign.hoursSubheading,
  };

  const hoursNote: TextSettings = {
    ...(defaults?.hoursNote as TextSettings),
    ...userDesign.hoursNote,
  };

  const contactInfoSubheading: TextSettings = {
    ...(defaults?.contactInfoSubheading as TextSettings),
    ...userDesign.contactInfoSubheading,
  };

  const contactInfoPhone: TextSettings = {
    ...(defaults?.contactInfoPhone as TextSettings),
    ...userDesign.contactInfoPhone,
  };

  const contactInfoEmail: TextSettings = {
    ...(defaults?.contactInfoEmail as TextSettings),
    ...userDesign.contactInfoEmail,
  };

  const socialProfiles: TextSettings = {
    ...(defaults?.socialProfiles as TextSettings),
    ...userDesign.socialProfiles,
  };

  const actionLink: ActionLinkSettings = {
    ...(defaults?.actionLink as ActionLinkSettings),
    ...userDesign.actionLink,
  };

  const image: ImageSettings = {
    ...defaults?.image,
    ...userDesign.image,
    overlay: {
      ...defaults?.image?.overlay,
      ...userDesign.image?.overlay,
      solid: {
        ...defaults?.image?.overlay?.solid,
        ...userDesign.image?.overlay?.solid,
      },
      gradient: {
        ...defaults?.image?.overlay?.gradient,
        ...userDesign.image?.overlay?.gradient,
      },
    },
  };

  return {
    layout: userDesign.layout,
    general,
    title,
    description,
    locationSubheading,
    locationAddress,
    locationMap,
    hoursSubheading,
    hoursNote,
    contactInfoSubheading,
    contactInfoPhone,
    contactInfoEmail,
    image,
    socialProfiles,
    actionLink,
  };
}

function layoutWithImage(
  layout: LocationTileLayout | undefined,
): boolean {
  return layout === 'IMAGE_RIGHT'
    || layout === 'IMAGE_LEFT'
    || layout === 'BACKGROUND_RIGHT'
    || layout === 'BACKGROUND_LEFT'
    || layout === 'FULL';
}

function layoutWithMap(
  layout: LocationTileLayout | undefined,
): boolean {
  return layout === 'MAP_RIGHT'
    || layout === 'MAP_LEFT'
    || layout === 'BACKGROUND_RIGHT'
    || layout === 'BACKGROUND_LEFT'
    || layout === 'FULL';
}

function tileStyles(
  design: LocationTileDesign,
  layoutHasImage: boolean,
  layoutHasMap: boolean,
  imageData: ImageData | undefined,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [
    ...generalStyles(design.general),
    ...textStyles('title', design.title),
    ...textStyles('description', design.description),
    ...textStyles('location-subheading', design.locationSubheading),
    ...textStyles('location-address', design.locationAddress),
    ...textStyles('hours-subheading', design.hoursSubheading),
    ...textStyles('hours-note', design.hoursNote),
    ...textStyles('contact-info-subheading', design.contactInfoSubheading),
    ...textStyles('contact-info-phone', design.contactInfoPhone),
    ...textStyles('contact-info-email', design.contactInfoEmail),
    ...actionLinkStyles('button', design.actionLink),
    ...colorStyles('--social-icon-color', design.socialProfiles?.color),
  ];

  if (layoutHasImage) {
    styles = [
      ...styles,
      ...imageStyles('image', imageData),
      ...imageOverlayStyles('image', design.image),
    ];
  }

  if (layoutHasMap) {
    styles = [
      ...styles,
      ...imageOverlayStyles('location-map', design.locationMap),
    ];
  }

  return styles;
}

const LocationTile = defineComponent({
  components: {
    ActionLink,
    ArrowDownIconSmall,
    DirectionsIcon,
    FacebookIcon,
    InstagramIcon,
    TwitterIcon,
    WhatsappIcon,
    YoutubeIcon,
    PinterestIcon,
    VkontakteIcon,
    MessengerIcon,
    TiktokIcon,
    YelpIcon,
    FontLoader,
    TileTitle,
  },
  props: {
    tileId: {
      required: true,
      type: String,
    },
    content: {
      required: true,
      type: Object as PropType<LocationTileContent>,
    },
    design: {
      required: true,
      type: Object as PropType<LocationTileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
    isHeadTile: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { attrs, root, emit }) {
    const i18n = useI18N(root.$store);
    const content = computed(() => props.content);
    const design = computed(() => {
      const defaults = getDefaults(props.design.layout);
      return getDesignSettings(props.design, defaults);
    });

    const titleContent = computed(() => content.value.title);
    const titleDesign = computed(() => design.value.title);
    const titleElement = useTextElement(titleContent, titleDesign);
    const showTitleOnTop = computed(() => design.value.layout === 'FULL');

    const descriptionContent = computed(() => content.value.description);
    const descriptionDesign = computed(() => design.value.description);
    const descriptionElement = useTextElement(descriptionContent, descriptionDesign);

    const imageContent = computed(() => content.value.image);
    const imageDesign = computed(() => design.value.image);
    const imageElement = useImageElement(imageContent, imageDesign);
    const layoutHasImage = computed(() => layoutWithImage(design.value.layout));
    const uploadingImage = computed(() => {
      const { imageListInUploadingState } = root.$store.state as State;
      return getImageInUploadingState(imageListInUploadingState, props.tileId, 'image');
    });
    const isUploadingImage = computed(() => uploadingImage.value !== undefined);

    const locationContent = computed(() => content.value.location);
    const locationSubheadingDesign = computed(() => design.value.locationSubheading);
    const locationAddressDesign = computed(() => design.value.locationAddress);
    const locationMapDesign = computed(() => design.value.locationMap);
    const locationElement = useLocationElement(
      locationContent,
      locationSubheadingDesign,
      locationAddressDesign,
      locationMapDesign,
    );
    const layoutHasMap = computed(() => layoutWithMap(design.value.layout));
    const isIconLocationGetDirections = computed(() => design.value.layout === 'SHORT');
    const isLinkLocationGetDirections = computed(() => design.value.layout !== 'SHORT');

    const hoursContent = computed(() => content.value.hours);
    const hoursSubheadingDesign = computed(() => design.value.hoursSubheading);
    const hoursNoteDesign = computed(() => design.value.hoursNote);
    const hoursElement = useHoursElement(hoursContent, hoursSubheadingDesign, hoursNoteDesign);

    const contactInfoContent = computed(() => content.value.contactInfo);
    const contactInfoSubheadingDesign = computed(() => design.value.contactInfoSubheading);
    const contactInfoPhoneDesign = computed(() => design.value.contactInfoPhone);
    const contactInfoEmailDesign = computed(() => design.value.contactInfoEmail);
    const contactInfoElement = useContactInfoElement(
      contactInfoContent,
      contactInfoSubheadingDesign,
      contactInfoPhoneDesign,
      contactInfoEmailDesign,
    );

    const socialProfilesContent = computed(() => content.value.socialProfiles);
    const socialProfilesDesign = computed(() => design.value.socialProfiles);
    const socialProfilesElement = useSocialIconListElement(
      socialProfilesContent,
      socialProfilesDesign,
    );

    const actionLinkContent = computed(() => content.value.actionLink);
    const actionLinkDesign = computed(() => design.value.actionLink);
    const isActionLinkHasElement = computed(
      () => hasActionLinkElement(actionLinkContent, actionLinkDesign),
    );

    const generalDesign = computed(() => design.value.general);

    const siteSettings = computed(() => (root.$store.state as State).siteSettings);
    const {
      tileElementRef,
      isTileShown,
      hasTransparentBackground,
    } = useTileBase(
      emit,
      props.viewportSettings,
      siteSettings,
      generalDesign,
    );

    const layoutClass = computed(() => `ins-tile--${computedLayoutClass(design.value.layout)}`);
    const fonts = computed(() => [
      titleDesign.value?.font,
      descriptionDesign.value?.font,
      locationSubheadingDesign.value?.font,
      locationAddressDesign.value?.font,
      hoursSubheadingDesign.value?.font,
      hoursNoteDesign.value?.font,
      contactInfoSubheadingDesign.value?.font,
      contactInfoPhoneDesign.value?.font,
      contactInfoEmailDesign.value?.font,
      actionLinkDesign.value?.font,
    ]);

    const styleSelector = computed(() => {
      const styles = tileStyles(
        design.value,
        layoutHasImage.value,
        layoutHasMap.value,
        content.value.image,
      );
      return tileStyleSelector(`#${attrs.id}`, styles);
    });
    const imageStyleSelector = computed(() => {
      if (!layoutHasImage.value) {
        return '';
      }

      const imageSet = getImageSet(
        root.$store.state.context,
        uploadingImage.value,
        imageElement.image,
      );

      const styles: string[] = [];
      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url600,
          image2x: imageSet?.url1200,
          backgroundColor: imageSet?.backgroundColor,
        },
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url1200,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_S,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--full .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url2000,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_L,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--background-right .ins-tile__image,
         #${attrs.id}.ins-tile--background-left .ins-tile__image`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url600,
          image2x: imageSet?.url1200,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_L,
      ));

      return styles.join('');
    });

    const isLoadingMap = ref(true);

    function setMapLoadingState(state: boolean): void {
      isLoadingMap.value = state;
    }

    return {
      i18n,
      titleElement,
      showTitleOnTop,
      descriptionElement,
      imageElement,
      isUploadingImage,
      layoutHasImage,
      layoutHasMap,
      locationElement,
      isIconLocationGetDirections,
      isLinkLocationGetDirections,
      hoursElement,
      contactInfoElement,
      socialProfilesElement,
      actionLinkContent,
      actionLinkDesign,
      isActionLinkHasElement,
      tileElementRef,
      isTileShown,
      hasTransparentBackground,
      layoutClass,
      fonts,
      styleSelector,
      imageStyleSelector,
      isLoadingMap,
      setMapLoadingState,
    };
  },
});

export default LocationTile;
